import { Container, Row, Col, Card } from 'react-bootstrap';
import myself from '../../image/myself.png';
import './Resume.css'

export default function Resume() {
    let personalData = [{
        name: 'Nacionalidad',
        value: 'Argentina'
    }, {
        name: 'Domicilio',
        value: 'San José, Temperley, Buenos Aires'
    }, {
        name: 'Email',
        value: 'RodriguezGonzalo97@gmail.com'
    }, {
        name: 'WhatsApp',
        value: '+54 911 3647 9848'
    }]

    let Skills = ["HTML", "CSS", "JavaScript", "React JS", "Redux", "Node JS", "Express", "Sequelize", "Passport", "PostgreSQL", "MySQL", "Git"]

    let text = "Luego de más de 700 horas de aprendizaje intensivo me siento listo para mi primer empleo formal como desarrollador. Las tecnologías aprendidas incluyen HTML, CSS, JavaScript, React, Redux, Node, Express, PostgreSQL, Sequelize, Git y la metodología ágil SCRUM."

    document.body.style = `
    background: linear-gradient(90deg, rgba(131,58,180,1) 7%, rgba(253,29,29,1) 67%, rgba(252,176,69,1) 100%);`

    // linear-gradient(90deg, rgba(131,58,180,1) 7%, rgba(253,29,29,1) 67%, rgba(252,176,69,1) 100%)
    return ( <Container fluid>
        <Row className="mt-4">
            <Col sm={12}>
                <Card className="text-center">
                    <Card.Header>
                        <Row>
                            <Col sm={12} lg={4}>
                                <img src={myself} className="resumeImg"/>
                            </Col>
                            <Col sm={12} lg={8}>
                                <div className="resumeHeaderDiv">
                                    <h1>Gonzalo Demian Rodriguez</h1>
                                    <div className="resumeDivider"></div>
                                    <h3>Full stack web developer</h3>
                                </div>
                            </Col>
                        </Row>
                    </Card.Header>
                    <Card.Body>
                        <Row>
                            <Col sm={12} lg={4}>
                                <h3>Datos personales</h3>
                                {personalData.map((v) => <div> <div className="resumeDivSpan">
                                    <span className="resumeSpan1">{v.name}</span>
                                    <span className="resumeSpan2">{v.value}</span>
                                    <div className="resumeDivider"></div>
                                </div>
                                <div className="resumeDivider"></div>
                                </div> )}
                            </Col>
                            <Col sm={12} lg={8}>
                                <h3>Skills</h3>
                                <div className="resumeSkills">
                                    {Skills.map((v) => <div className="resumeSpan3">{v}</div>)}
                                </div>
                            </Col>
                        </Row>
                    </Card.Body>
                </Card>
            </Col>
            {/* <Col>
            asdasd
            </Col> */}
        </Row>
    </Container> )
}