
import gsap, { TimelineLite, Power3 } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import { TextPlugin } from 'gsap/TextPlugin'
import { Container, Row, Col } from 'react-bootstrap';
import { useEffect, useRef, useState } from 'react';
import './Home2.css';
import myself from '../../image/myself.png';
import img from '../../image/playBlack3.png';
import imgReact from '../../image/react.png';
import imgReactHex from '../../image/hexagon.png';
import imgHtml from '../../image/html1.png';
import imgCss from '../../image/css.png';
import imgJs from '../../image/js.png';
import imgRedux from '../../image/redux.png';
import imgNode from '../../image/nodejs.png';
import imgSql from '../../image/sql.png';
import imgGit from '../../image/git.png';
import imgArg from '../../image/arg.png';
import imgIng from '../../image/ing.png';
import imgLinkedIn from '../../image/linkedin.png';

gsap.registerPlugin(ScrollTrigger);
gsap.registerPlugin(TextPlugin);

var subTitle = ['años - Argentino de Buenos Aires - Graduado en Henry', 'years old - Argentinian from Buenos Aires - Henry Graduate']

var mySkills = ['Mis habilidades', 'My skills']
var myStory = [['Mi historia', 'My story'], [
    'Empecé a programar a los 13 años en el lenguaje Pawn (Plugins para AmxModX, modulo que permite modificar el motor de juego GoldSrc). \
    A los 14 empecé con SQLite. Formé mi propia comunidad de juegos llamada Ancestral-Games (para ver en YT buscar "Ancestral-Games BCM"). A los 16 desarrollé un programa simple en C++. \
    A los 19 empecé con PHP. A los 21 conocí Vue.JS y desarrollé una SPA con Vuetify para Ancestral-Games con back en PHP con MySQL. A mis 24 me gradué como Full Stack \
    Web Developer en el Henry BootCamp, donde aprendí a fondo JavaScript (ES6), HTML5, CSS3, JQuery y el stack PERN [PostgreSQL, Express.JS, React.JS y Node.JS]. Todo \
    esto fue consolidado con la metodología ágil SCRUM en las últimas dos instancias en Henry, el primer proyecto fue una SPA ecommerce desde 0 y el segundo proyecto \
    una app con React Native (Android/iOS) + panel de administración web con React.JS y TypeScript en Express.JS.',
    
    'I started programming at the age of 13 in the Pawn language (Plugins for AmxModX, a module that allows modifying the GoldSrc game engine). \
    At 14 I started with SQLite. I created my own gaming community called Ancestral-Games (to see on YT search for "Ancestral-Games BCM"). At 16 I developed a simple C ++ program. \
    At 19 I started with PHP. At 21 I met Vue.JS and developed a SPA for Ancestral-Games with Vuetify and PHP with MySQL in back. At 24 I graduated as a Full Stack Web Developer at Henry BootCamp, \
    where I thoroughly learned JavaScript (ES6), HTML5, CSS3, JQuery and the PERN stack [PostgreSQL, Express.JS, React.JS and Node.JS]. All this was consolidated with the SCRUM agile methodology \
    in the last two instances in Henry, the first project was an ecommerce SPA from scratch and the second project an app with React Native (Android/iOS) + web administration panel \
    with React.JS and TypeScript in Express.JS.'
]]

export default function Home2() {
    const [lang, setLang] = useState(0);

    let changeLang = (l) => {
        setLang(l)
        gsap.to('.parrafo1', {
            text: {
                value: age +" "+ subTitle[l],
                speed: 1,
                padSpace: true
            },
            ease: "none"
        })

        /* if (l) { // english
            gsap.to('.parrafo1', {
                text: {
                    value: subTitle[l]
                }
            })
        } else { // spanish
            gsap.to('.parrafo1', {
                text: {
                    value: "ESTE ES EL NUEVO TEXTO2"
                }
            })
        } */
    }

    let test = useRef(null);
    let t1 = gsap.timeline()//new TimelineLite({ delay: 0.3 })
    let t2 = gsap.timeline();

    useEffect(() => {
        t1.staggerFrom('.lang', 0.35, {
            opacity: 0,
            x: -50,
            ease: Power3.easeOut
        })
        .from('.myImg', {
            opacity: 0,
            y: -250,
            ease: Power3.easeOut
        })
        .from('#name', {
            opacity: 0,
            x: -50,
            ease: Power3.easeOut
        })
        .from('.linea', {
            opacity: 0,
            x: 50,
            ease: Power3.easeOut
        })
        .from('#subTitle', {
            opacity: 0,
            x: -50,
            ease: Power3.easeOut
        })
        .from('.parrafo1', {
            opacity: 0,
            x: 50,
            ease: Power3.easeOut
        })

        t2.from('#title1', {
            opacity: 0,
            y: 50,
            /* ease: Power3.easeOut,
            duration: 1.5, */
            scrollTrigger: {
                trigger: '#title1',
                start: 'top 90%',
                end: 'bottom 20%',
                scrub: true
                // markers: true,
                // toggleActions: 'restart complete reverse reset'
            }
        })
        t2.from('.skillCard', {
            opacity: 0,
            x: -100,
            /* ease: Power3.easeOut,
            duration: 1.5,*/
            stagger: {
                each: 0.23
            }, 
            scrollTrigger: {
                trigger: '#title1',
                start: 'top 90%',
                endTrigger: '.skillCardEnd',
                end: 'top 75%',
                scrub: true,
                // markers: true,
                // onEnter onLeave onEnterBack onLeaveBack
                // actions: "play", "pause", "resume", "reset", "restart", "complete", "reverse", and "none"
                // example:
                // toggleActions: 'restart complete reverse reset'
            }
        })

        t2.from('#title2', {
            opacity: 0,
            y: -50,
            scrollTrigger: {
                trigger: '#title2',
                start: 'top 90%',
                end: 'bottom 50%',
                toggleActions: 'play complete none reset',
                // markers: true,
                scrub: true,
            }
        })
        t2.from('#parrafo2', {
            opacity: 0,
            y: 100,
            scrollTrigger: {
                trigger: '#title2',
                endTrigger: '#parrafo2',
                start: 'top 90%',
                end: 'bottom 80%',
                toggleActions: 'play complete none reset',
                scrub: true,
                // markers: true
            }
        })
    }, [])

    let day = 2;
    let month = 4;
    let year = 1997;

    let today = new Date();
    let dd = today.getDate();
    let mm = today.getMonth() + 1; //January is 0!
    let yyyy = today.getFullYear();

    let age = (yyyy - year) - (!((dd >= day && mm === month) || (mm > month)) ? 1 : 0);

    let skills = [{name: 'HTML', txt: 'HTML5', img: imgHtml}, {name: 'CSS', txt: 'CSS3', img: imgCss},
    {name: 'ES 6', txt: 'JavaScript', img: imgJs}, {name: 'React', txt: 'React JS', img: imgReact},
    {name: 'Redux', txt: 'Redux', img: imgRedux}, {name: 'Node', txt: 'Node JS', img: imgNode},
    {name: 'Git', txt: 'Git', img: imgGit}, {name: 'SQL', txt: 'PostgreSQL, MySQL y SQLite', img: imgSql}]

    return (
        <div>
            {/* Main */}
            <section className="sectionA">
                <img src={myself} className="myImg" />
                <h1 id="name" style={{opacity: 1}}>Gonzalo Demian Rodriguez</h1>
                <div className="linea"></div>
                <h2 id="subTitle">Full Stack Web Developer</h2>
                <p className="parrafo1">{age} {subTitle[0]}</p>
            </section>
            {/* Skills */}
            <section className="sectionB">
                <h1 id="title1">{mySkills[lang]}</h1>

                {skills.map(v => (<div className="skillCard">
                    <img src={v.img} alt={v.name} className="imgSkill"/>
                    <div>
                        <h3>{v.name}</h3>
                    </div>
                </div>))}
                <div className="skillCardEnd"></div>
            </section>
            {/* About */}
            <section className="sectionC">
                <h1 id="title2">{myStory[0][lang]}</h1>
                <p id="parrafo2">{myStory[1][lang]}</p>
            </section>
            {/* Resume */}
            <section className="sectionD">
                
                {/* <p>{lang ? 'Made with' : 'Hecho con'} <span style={{color: 'red'}}>♥</span> {lang ? 'by' : 'por'} <span style={{color: 'rgb(0, 238, 255)'}}>Gonza.-*</span></p> */}
                <div><a href="mailto:RodriguezGonzalo97@gmail.com">RodriguezGonzalo97@gmail.com</a> <a target="_blank" href="https://www.linkedin.com/in/gonzalo-demian-rodriguez-ab40a1157"><img src={imgLinkedIn} alt="LinkedIn" /></a></div>
            </section>
            {/* Portfolio */}
            {/* <section className="sectionE">
                <p ref={e => (test = e)}>Lorem ipsum dolor sit amet consectetur adipisicing elit. Cumque nisi blanditiis ea facilis optio voluptates atque necessitatibus unde ut esse aspernatur, voluptatem quam modi illum vitae? Ullam vitae voluptatem illum nesciunt quam autem cumque, saepe minus earum, nostrum blanditiis nam doloribus asperiores beatae vel ipsa. Sit, molestiae enim rem ea voluptates laudantium quas itaque delectus unde minima. Libero, minus! Porro sint dolorem debitis obcaecati quia itaque reprehenderit at, pariatur quos iste aliquid fuga? Eaque, minima accusamus libero consequuntur unde sit sequi tenetur tempora dignissimos ipsum ipsa quas odit totam provident nesciunt cupiditate soluta repellendus deleniti ab. Totam deserunt itaque expedita quis dolorem corrupti porro in illo atque iure sapiente quasi obcaecati maiores natus, voluptates maxime esse consectetur deleniti, rerum aperiam ea! Ex odio optio nobis, esse mollitia quas iste omnis eaque officiis blanditiis, consequatur veritatis iusto incidunt corrupti. Alias, ex mollitia. Quod numquam sit vero architecto omnis ratione delectus placeat!</p>
            </section>
            <section className="sectionF">
                <p ref={e => (test = e)}>Lorem ipsum dolor sit amet consectetur adipisicing elit. Cumque nisi blanditiis ea facilis optio voluptates atque necessitatibus unde ut esse aspernatur, voluptatem quam modi illum vitae? Ullam vitae voluptatem illum nesciunt quam autem cumque, saepe minus earum, nostrum blanditiis nam doloribus asperiores beatae vel ipsa. Sit, molestiae enim rem ea voluptates laudantium quas itaque delectus unde minima. Libero, minus! Porro sint dolorem debitis obcaecati quia itaque reprehenderit at, pariatur quos iste aliquid fuga? Eaque, minima accusamus libero consequuntur unde sit sequi tenetur tempora dignissimos ipsum ipsa quas odit totam provident nesciunt cupiditate soluta repellendus deleniti ab. Totam deserunt itaque expedita quis dolorem corrupti porro in illo atque iure sapiente quasi obcaecati maiores natus, voluptates maxime esse consectetur deleniti, rerum aperiam ea! Ex odio optio nobis, esse mollitia quas iste omnis eaque officiis blanditiis, consequatur veritatis iusto incidunt corrupti. Alias, ex mollitia. Quod numquam sit vero architecto omnis ratione delectus placeat!</p>
            </section> */}

            <div className="lang">
                {/* <div>Español</div>
                <div>English</div> */}
                <img id="img1" onClick={e => {e.preventDefault(); changeLang(0)}} src={imgArg} alt="Español" />
                <img id="img2" onClick={e => {e.preventDefault(); changeLang(1)}} src={imgIng} alt="English" />
            </div>
        </div>
    )
}