import { Container, Row, Col, Card } from 'react-bootstrap';
import './Portfolio.css';

export default function Portfolio() {
    let portFolio = [{
        name: 'Todo List App',
        description: 'Una todo list simple pero atractiva, en una sola página (sin router).',
        technologies: ['React', 'Redux', 'JavaScript', 'CSS', 'LocalStorage', 'Node'],
        url: 'https://gonzag3x.github.io/todo',
        img: null
    }, {
        name: 'We are Wine',
        description: 'Ecommerce de bebidas alcohólicas (por deployar)',
        technologies: ['React', 'Redux', 'JavaScript', 'CSS', 'LocalStorage', 'Node', 'PostgreSQL', 'Express', 'Sequelize', 'Passport'],
        url: null,
        img: null
    }]

    document.body.style = `
    background: linear-gradient(90deg, rgba(0,212,255,1) 0%, rgba(24,121,9,1) 81%);`

    return ( <Container className="mt-1">
        <Row>
            {portFolio.map(v => <Col sm={3} style={{margin: '10px 0px'}}>
                <Card style={{cursor: 'pointer'}} onClick={e => {
                    e.preventDefault();
                    if (v.url) window.open(v.url)
                }}>
                    <Card.Header className="text-center">
                        <h2>{v.name}</h2>
                    </Card.Header>
                    <Card.Body className="pfCardBody">
                        <Card.Text>{v.description}</Card.Text>
                        {v.technologies.map(v => <div className="pfTech">{v}</div> )}
                    </Card.Body>
                </Card>
            </Col>)}
        </Row>
    </Container> )
}